@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Display:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

.cover-container {
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0,0.6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.video{
    object-fit: cover;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;
}
.cover-container > h1{
    color: white;
    font-size: 90px;
}
.cover-container>p{
    color: white;
    margin-top: 8px;
    font-size: 30px;
}

@media screen and (max-width: 960px){
    .cover-container>h1{
        font-size: 70px;
        text-align: center;
    }
    .cover-container>p{
        text-align: center;
        font-size: 22px;
        width: 85%;
    }
}
.navbar{
    display: flex;
    flex-direction: row;
    position: fixed;
    top: 0;
    justify-content: flex-start;
    align-items: center;
    background-color: transparent;
    width: 100%;
    height: 80px;
    font-size: 20px;
    z-index: 1;
    grid-gap: 20px;
    gap: 20px;
}
.linksNavbar{
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    margin-left: 20px;
}
.scrolling {
    background-color: black;
    transition: all 0.5s ease;
}
.navbar-logo{
    height: 80px;
    line-height: 80px;
    color: white;
    cursor: pointer;
}


.lang-container{
    position: absolute;
    right: 0px;
}

.flag-icon{
    background-size: cover;
    line-height: 2;
    width: 30px;
    height: 25px;
}
.es, .en{
    border: none;
    margin-right: 10px;
}
.es-clicked{
    padding-bottom: 5px;
    background: transparent;
    border: none;
    border-bottom: 4px solid white;
    transition: all 0.3s ease-out;
    margin-right: 10px;
}
.en-clicked{
    padding-bottom: 5px;
    background: transparent;
    border: none;
    border-bottom: 4px solid white;
    transition: all 0.3s ease-out;
    margin-right: 10px;
}
@media screen and (max-width: 960px){
    .lang-container{
        position: relative;
    }
    
    .navbar{
        font-size: 16px;
        font-weight: 500;
        justify-content: center;
    }
    .linksNavbar{
        margin-left: 0px;
        grid-gap: 15px;
        gap: 15px;
    }
}
.about-container{
    display: flex;
    background-color: white;
    color: black;
    height: 70vh;
    padding: 100px 0 100px 0;
}

.about-desc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 55%;
    padding: 0 100px 0 100px;
}
.about-desc > h3{
    
    margin-bottom: 30px;
    font-size: 28px;

}
.about-desc > p{
    line-height: 1.5;
}

.about-img {
    display: flex;
    flex-direction: column;
    align-self: center;
    width: 45%;
    overflow: hidden;
}
@media screen and (max-width: 960px){
    .about-container{
        flex-direction: column;
        padding: 0;
        height: auto;
    }
    .about-desc{
        width: 90%;
        margin: 0 auto;
        padding: 40px 20px 50px 20px;
    }
    .about-desc>h3{
        font-size: 20px;
        text-align: center;
    }
    .about-img{
        width: 100%;
        margin-bottom: 50px;
    }
    .about-img>img{
        width: 100%;
    }
}
.carousel-container{
    display: flex;
    vertical-align: center;
    align-items: center;
    background-color: black;
    padding: 30px 0 30px 0;
}
.carousel-title{
    color: white;
    width: 40%;
    text-align: center;
    font-size: 30px;
    padding:0 100px 0 100px ;
    
}

.slide-container{
    height: 80%;
    border-radius: 40px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
}

.slide-container > img{
    object-fit: contain;
    transition: all 0.2s ease;
}

.slide-container:hover>img{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0.7;
}

.slide-desc{
    background-color: black;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 70%;
    padding-left: 30px;
    border-top-left-radius: 20px;
    opacity: 0.8;
}

.slide-desc>span{
    color: white;
    display: block;
    font-size: 22px;
    padding: 20px 0 20px 10px;
}

.BrainhubCarousel__arrows{
    background-color: white;
    border-radius: 50%;
    margin: 20px;
}
.BrainhubCarousel__arrows>span{
    border-color: black;
}
.BrainhubCarousel__arrows:hover{
    background-color: white !important;

}
.BrainhubCarousel__arrows:hover>span{
    border-color: black !important;
}

@media screen and (max-width: 960px){
    .carousel-container{
        flex-direction: column;
    }
    .carousel-title{
        width: 100%;
        text-align: center;
        padding: 50px 0 0px 0;
        
        
    }
    .slide-container{ 
        
    }
}
.info-container{
    width: 100%;
    height: 300px;
    background-color: white;
    color: black;
    margin-bottom: 100vh;
}
.info{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
@media screen and (max-width: 960px){
    .info-container{
        height: 200px;
        width: 100%;

    }
    .info>h1{
        text-align: center;
        font-size: 25px;

    }
}


.footer{
    width: 100%;
    height: 100vh;
    background-color: black;
    color: white;
    padding-top: 120px;
    position: fixed;
    z-index: -2;
    bottom: 0;
    left: 0;
}

.footer-info{
    display: flex;
    flex-direction: row;
    font-size: 35px;
    padding: 30px 0 30px 50px;
    width: 70%;
    margin: 0 auto;

}
.contactContainer{
    width: 30%;
    padding: 30px 0 30px 0;
    margin-left: 15%;
}
.contactContainer>form{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    margin-top: 20px;
}
input, textarea{
    color: white;
    border-radius: 10px;
    background-color: black;
    border: none;
    border: 1px solid white;
    height: 45px;
}
textarea{border-radius: 0;}
.contactContainer>form>button{
    width: 100px;}
.inputBot{
    width: 100px;
    cursor: pointer;
    color: white;
    border-radius: 10px;
    background-color: black;
    border: none;
    border: 1px solid white;
    height: 30px;
}
.inputBot:hover{-webkit-transform: scale(1.1);transform: scale(1.1);}   

.inputMess{
    height: 70px;
}
.country{
    display: flex;
    flex-direction: row;
    
}
.globo{
    margin-left: 10px;
}

.footer-info>h1{
    padding-bottom: 15px;
}

.footer-sns{
    width: 70%;
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}


.sns-links{
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    
}

.linkedin, .whatsapp, .instagram, .email{
    color: white;
    font-size: 35px;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 4px solid transparent;
}
.linkedin:hover, .whatsapp:hover, .instagram:hover, .email:hover{
    border-color: rgb(255, 255, 255);
    transition: all 0.3s ease-out;
}

.design-by{
    font-size: 18px;
    padding-left: 50px;
    color: white;
}

@media screen and (max-width: 960px){
    .footer-info{
        text-align: center;
        padding: 0;
        font-size: 20px;
        margin-top: 0px;
        flex-direction: column;
    }
    
   
    .contactContainer{
        width: 100%;
        padding: 17% 0 0 0;
        margin: 0

    }
    .footer-sns{
        grid-template-columns: 1fr;
        text-align: center;
    }
    .design-by{
        display: none;
    }

    .country{
        justify-content: center;
   }
   
}



.sidebarContainer{
    width: 100%;
    height: 400px;
    background-color: rgb(253, 253, 253);
    color: black;
    margin-bottom: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    
}
.sideInfoContainer{
    width: 50%;
    padding: 20px;
    border: 1px solid lightblue;
    border-left-color: transparent;
    height: auto;
}

.sideMenuContainer{
    display: flex;
    flex-direction: column;
    width: 15%;
    margin-left: 15%; 
}

.sectionSpan{
    text-align: center;
    color: rgb(0, 0, 0);
    background-color: white;
    padding: 15px;
    border: 1px solid rgb(8, 137, 141);
    
}
.sectionSpan:hover{
    cursor: pointer;
}
.educacion, .skills, .jobexp{
    text-align: center;
    color: rgb(253, 253, 253);
    padding: 15px;
    background: radial-gradient(rgb(9, 66, 88), rgb(8, 137, 141)) ; ;
    transition: all 0.3s ease-out;
    border-radius: 5px;
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
    
}
.educationP{
    font-family: 'Noto Sans Display', sans-serif;
}

.mystackP{
    font-family: 'Noto Sans Display', sans-serif;
    font-size: 17px;
}
.idiomasLevel{
    margin-top: 2%;
    display: flex;
    flex-direction: row;
}

.span{
    margin-right: 25px;
    font-weight: 400;
    font-family: 'Noto Sans Display', sans-serif;
    
}
.skillsContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
.progrLeng{
    width: 100%;
    display: flex;
    justify-content: center;
    grid-gap: 30px;
    gap: 30px;
    margin-top: 15px;
    margin-bottom: 10px;
}
.js, .html5, .css3, .react, .wordpress{
    font-size: 35px;
    cursor: pointer;
}

.jsSelected, .html5Selected, .css3Selected, .reactSelected, .wordpressSelected{
    font-size: 35px;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    padding-bottom: 4px;
    border-bottom: 2px solid rgb(8, 137, 141); 
}

.fa-star{
    font-size: 20px;
    margin-bottom: 10px;
}
@media screen and (max-width: 960px){
    .sidebarContainer{
        flex-direction: column;
        height: 500px;
    }
    .sideInfoContainer{
        width: 100%;
        padding: 20px;
        border: 1px solid lightblue;
        border-left-color: transparent;
        border-right-color: transparent;
        height: auto;
        margin-top: 20px;
    }
    
    .sideMenuContainer{
        display: flex;
        flex-direction: row;
        width: 100;
        justify-content: center;
        margin-left: 0px;
        margin-top: 15px;
    }
    .sectionSpan{
        text-align: center;
        align-self: center;
        color: rgb(2, 2, 2);
        padding: 5px;
        background: white;
        border: 1px solid rgb(8, 137, 141);
        transition: all 0.3s ease-out;
        
    }
    .educacion, .skills, .jobexp{
        text-align: center;
        color: rgb(253, 253, 253);
        padding: 5px;
        background: radial-gradient(rgb(9, 66, 88), rgb(8, 137, 141)) ; ;
        transition: all 0.3s ease-out;
        border-radius: 5px;
        align-self: center;
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        
    }
    .idiomasLevel{
        display: flex;
        flex-direction: column;
        grid-gap: 5px;
        gap: 5px;
        
    }
    .banderaLevel{
        display: flex;
        flex-direction: row;
    }
    
    

}
